import { NgModule } from '@angular/core';
import { Routes, RouterModule, ActivatedRoute } from '@angular/router';
import { DepartmentListComponent } from './department-list/department-list.component';
import { DepartmentDetailComponent } from './department-detail/department-detail.component';
import { EmployeeListComponent } from './employee-list/employee-list.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { DepartmentOverviewComponent } from './department-overview/department-overview.component';
import { DepartmentContactComponent } from './department-contact/department-contact.component';
import { Akid } from './akid/akid';
import { acx } from './acx/acx';
import { blg } from './blg/blg';
import { blgm } from './blgm/blgm';


export const routingComponents = [DepartmentListComponent,
                                  DepartmentDetailComponent, 
                                  EmployeeListComponent,
                                  DepartmentOverviewComponent,
                                  DepartmentContactComponent,
                                  Akid,
                                  acx,
                                  blg,
                                  blgm,
                                  PageNotFoundComponent]

const routes: Routes = [
  { path: '', redirectTo: '/blog', pathMatch: 'full' },
  { path: 'departments', component: DepartmentListComponent },
  { path: 'departments/:id', component: DepartmentDetailComponent,
    children: [
      { path: 'overview', component: DepartmentOverviewComponent},
      { path: 'contact', component: DepartmentContactComponent}
    ]
   },
  { path: 'employees',   component: EmployeeListComponent },
  { path: 'akid/:id',   component: Akid },
  { path: 'acx',   component: acx },
  { path: 'blog',   component: blgm },
  { path: 'blog/:id/:title',   component: blg },
  { path: '**',   component: PageNotFoundComponent }
  
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



