import { Component, OnInit } from '@angular/core';
import {clone} from 'lodash';
import { Router, ActivatedRoute, ParamMap} from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Meta, Title } from "@angular/platform-browser";
import 'rxjs/Rx';
@Component({
  selector: 'blgm-page',
  templateUrl: './blgm.html',
  styleUrls: ['./blgm.css']
})

export class blgm implements OnInit {
content:any
date:any
p: number = 1;
wx:any
  constructor(meta: Meta, title: Title, public http:Http, private router: Router, private route: ActivatedRoute) {
   this.date = new Date();
   this.wx = location.href
   // title.setTitle('Appsthink Blog');

    // meta.addTags([
    //   { name: 'author',   content: 'appsthink.com'},
    //   { name: 'keywords', content: 'angular blog'},
    //   { name: 'description', content: 'enjoy as always' }
    // ]);

    this.route.paramMap.subscribe((params: ParamMap) => {
   
      this.http.get('http://appsthink.com:1300/api/blg').map(res => res.json()).subscribe(resp => {
      this.content = resp
      for (var i = 0; i < this.content.length; i++) {
        var ipo = this.content[i].title.replace(/ /g,'-')
        console.log({url:ipo});
    this.content[i].url = ipo
    title.setTitle(this.content[i].title);
    meta.addTags([
      { name: 'title',   content: this.content[i].title},
      { name: 'content', content: this.content[i].content},
      { name: 'description', content: this.content[i].content},
      { name: 'url', content: 'http://appsthink.com/blog/'+this.content[i].blogid+'/'+this.content[i].url}
    ]);
    
    }
      console.log(this.content);
    })
    } );

   }

  ngOnInit() {
    console.log('blg');

    


  }
  
}



