import { Component, OnInit } from '@angular/core';
import {clone} from 'lodash';
import { Router, ActivatedRoute, ParamMap} from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/Rx';
import { Meta, Title } from "@angular/platform-browser";
@Component({
  selector: 'blg-page',
  templateUrl: './blg.html',
  styleUrls: ['./blg.css']
})
export class blg implements OnInit {
content:any
date:any
sugg:any
wx:any
  constructor(meta: Meta, title: Title, public http:Http, private router: Router, private route: ActivatedRoute) {
   this.date = new Date();
   this.wx = location.href;

   this.route.paramMap.subscribe((params: ParamMap) => {
      let id = parseInt(params.get('id'));
      console.log(id);
      let tt = params.get('title');
      console.log(tt.replace(/-/g,' ')); 
      this.http.get('http://appsthink.com:1300/api/blg?filter[blogid]='+id).map(res => res.json()).subscribe(resp => {
      this.content = resp[0]
      console.log(this.content);
      title.setTitle(this.content.title);
    meta.addTags([
      { name: 'title',   content: this.content.title},
      { name: 'description', content: this.content.content},
      { name: 'url', content: 'http://appsthink.com/blog/'+this.content.blogid+'/'+tt}
    ]);
    })
    } );

   this.http.get('http://appsthink.com:1300/sugg').map(res => res.json()).subscribe(resp => {
     this.sugg = resp
   })


   }

  ngOnInit() {
    console.log('blg');

    

    
  }
  
}



